.we-modest {
	padding-top: 81px;
	padding-bottom: 100px;
	@include r(768) {
		padding-top: 30px;
		padding-bottom: 40px; } }
.we-modest__h2 {
	color: $h2-color-mod;
	margin-bottom: 50px;
	@include r(768) {
		margin-bottom: 15px; } }
.modest-wrapper {
	font: $main-text-style;
	color: $main-text-color;
	@include r(768) {
		font: $main-text-style-mobile; } }
.left-col {
	vertical-align: top;
	@include clr;
	display: inline-block;
	width: 658px;
	@include r(1200) {
		width: 440px; }
	@include r(992) {
		width: 340px; }
	@include r(768) {
		width: 100%; }
	p {
		margin-bottom: 25px;
		&:last-of-type {
			margin-bottom: 48px; } }
	span {
		font-weight: 700; } }
.right-col {
	vertical-align: top;
	width: 457px;
	display: inline-block;
	@include r(992) {
		width: 370px; }
	@include r(768) {
		width: 100%; } }
.social-wrapper {
	@include clr;
	@include r(768) {
		text-align: center;
		margin-bottom: 25px; } }
a.modest-social {
	float: left;
	width: 60px;
	height: 60px;
	background: #c8cdd0;
	transition: all .3s;
	position: relative;
	margin-right: 1px;
	&:last-of-type {
		margin-right: 0; }
	.icon {
		@include vcenter;
		height: 1.3em;
		width: 1.3em;
		@include rmin(1200) {
			transition: all .3s; } }
	&:hover {
		background: #d0d5d9; }
	@include rmin(1200) {
		&:hover {
			.icon {
				height: 1.5em;
				width: 1.5em; } } }
	@include r(768) {
		width: 45px;
		height: 45px;
		float: none;
		display: inline-block;
		margin-right: 5px; } }
.we-modest__list {
	padding-left: 90px;
	@include r(768) {
		padding-left: 50px; }
	.list__item {
		position: relative;
		&:nth-of-type(2) {
			margin-bottom: 30px; }
		@include r(768) {
			margin-bottom: 20px;
			&:nth-of-type(2) {
				margin-bottom: 20px; } }
		.item-number {
			position: absolute;
			left: -66px;
			top: 8px;
			width: 50px;
			height: 50px;
			background: #23b7a4;
			border: 1px solid #23b7a4;
			text-align: center;
			color: #fff;
			font: 700 20px/50px "Quattrocento Sans";
			text-transform: uppercase;
			@include r(768) {
				left: -45px;
				top: 6px;
				width: 40px;
				height: 40px;
				line-height: 40px; } }
		@include rmin(1200) {
			.item-number {
				z-index: 1;
				&:before {
					transition: all .4s;
					content: '';
					position: absolute;
					@include vcenter;
					width: 0%;
					height: 0%;
					z-index: -1;
					background: transparent; } }
			&:hover {
				.item-number {
					color: #23b7a4;
					&:before {
						width: 100%;
						height: 100%;
						background: #fff; } } } } }
	span {
		color: #292f36;
		font: 700 20px/32px "Quattrocento Sans";
		text-transform: uppercase;
		letter-spacing: -2px;
		@include r(768) {
			font-size: 16px;
			line-height: 24px;
			letter-spacing: 0; } } }
