.contacts__h2 {
	color: $h2-color-mod;
	word-spacing: -7px;
	letter-spacing: 1px;
	margin-bottom: 55px;
	@include r(768) {
		margin-bottom: 20px; } }
.contacts {
	padding-top: 64px;
	padding-bottom: 97px;
	@include r(786) {
		padding-top: 30px;
		padding-bottom: 40px; } }
.map {
	width: 100%;
	height: 350px; }
.after-map-container {
	p {
		font: $main-text-style;
		color: $main-text-color;
		word-spacing: 6px;
		letter-spacing: -1px;
		margin-bottom: 26px;
		&:last-of-type {
			margin-bottom: 0; }
		span {
			font-weight: 700; }
		@include r(768) {
			font: $main-text-style-mobile; } } }
.contacts__text-block {
	width: 550px;
	padding-top: 54px;
	@include r(1200) {
		width: 450px; }
	@include r(992) {
		width: 365px; }
	@include r(768) {
		width: 100%;
		padding-top: 24px; }
	&>span {
		color: #292f36;
		font: 700 20px "Quattrocento Sans";
		text-transform: uppercase;
		display: block;
		margin-bottom: 28px;
		letter-spacing: -1px; } }
.callback-form {
	position: absolute;
	right: 15px;
	top: -132px;
	display: block;
	width: 555px;
	padding: 28px 30px;
	background: #fff;
	color: #8d8f92;
	font: 700 14px "Quattrocento Sans";
	text-transform: uppercase;
	@include r(1200) {
		width: 500px; }
	@include r(992) {
		width: 350px; }
	@include r(768) {
		position: relative;
		top: 0;
		right: 0;
		padding: 15px 0;
		width: 100%; }
	span {
		display: block;
		padding-left: 6px;
		margin-bottom: 5px; }
	.input-wrapper {
		display: inline-block;
		width: 240px;
		@include r(1200) {
			width: 205px; }
		@include r(992) {
			width: 100%; }
		&:first-of-type {
			margin-right: 10px; } }
	input, textarea {
		font: $main-text-style;
		color: #000;
		padding: 10px 15px;
		background: #eaeef1;
		border-radius: 3px;
		border: 1px solid #eaeef1;
		transition: all .4s;
		&:focus {
			border: 1px solid #23b7a4; } }
	input {
			width: 100%;
			@include r(768) {
				margin-bottom: 10px; } }
	textarea {
		display: block;
		width: 100%;
		resize: none;
		height: 160px; }
	.textarea-wrapper {
		margin-top: 27px;
		margin-bottom: 30px; } }
.callback-button {
	padding: 12px 23px;
	background: #23b7a4;
	border: 2px solid #23b7a4;
	border-radius: 3px;
	@include rmin(1200) {
		&:before {
			background: #fff; }
		&:hover {
			color: #23b7a4;
			&:before {
				width: 100%; } } } }
