footer {
	background: #3a454d;
	color: $main-text-color;
	font: 400 14px "Open Sans";
	padding-top: 39px;
	padding-bottom: 39px;
	@include r(768) {
		padding: 20px 0;
		text-align: center; }
	.container {
		@include clr; }
	p {
		float: left;
		@include r(768) {
			float: none; } } }
.footer-socials {
	float: right;
	@include r(768) {
			float: none;
			margin-top: 20px; }
	a {
		display: inline-block;
		margin-right: 13px;
		vertical-align: top;
		position: relative;
		transition: all .3s;
		top: 0;
		&:hover {
			top: -2px; }
		@include r(768) {
			margin-right: 20px; }
		&:last-of-type {
			margin-right: 0; }
		.icon {
			width: 1.2em;
			height: 1.2em; }

		.icon-e {
			width: 1.4em;
			height: 1.4em; } } }
