.our-work__h2 {
	letter-spacing: -3px;
	margin-bottom: 55px; }
.our-work {
	padding-top: 78px;
	background: #3a454d;
	padding-bottom: 96px;
	@include r(768) {
		padding-top: 30px; } }
.our-work__gallery {
	text-align: center;
	.gallery__item {
		display: inline-block;
		position: relative;
		width: 360px;
		font: 700 17px "Quattrocento Sans";
		color: #fff;
		text-transform: uppercase;
		margin-bottom: 22px;
		overflow: hidden;
		width: 360px;
		height: 265px;
		@include rmin(1200) {
			margin-right: 24px;
			&:nth-of-type(3n+3) {
				margin-right: 0; }
			&:hover {
				a {
					&:before {
						opacity: 1; }
					&:after {
						opacity: .3; } }
				p {
					bottom: 0; } } }
		@include r(1200) {
			width: 300px;
			margin-left: 10px;
			margin-right: 10px; }
		@include r(768) {
			margin: 0 5px 30px;
			width: 280px;
			height: 205px; }
		img {
			width: 100%;
			height: 100%; }
		a {
			display: block;
			position: relative;
			z-index: 2;
			width: 100%;
			height: 100%;
			&:after {
				@include coverdiv;
				content: '';
				background: #000;
				opacity: 0;
				transition: all .4s;
				z-index: 1; }
			&:before {
				transition: all .4s;
				opacity: 0;
				content: '+';
				@include vcenter;
				color: #fff;
				top: 35%;
				font-size: 50px;
				font-weight: 700;
				z-index: 2; } }
		p {
			text-align: left;
			transition: all .4s;
			width: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			background: #292f36;
			padding: 23px 18px;
			z-index: 2;
			@include rmin(1200) {
				bottom: -100px; } } } }
.more-galery {
	padding: 12px	20px;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: -35px;
	@include r(1200) {
		bottom: -45px; }
	@include r(768) {
		bottom: -46px; } }
